<template>
  <div>
    <ul>
      <li v-for="m in messageList" :key="m.id">
<!--        <router-link :to="`/home/message/detail?id=${m.id}&title=${m.title}`">{{m.title}}</router-link>-->
        <router-link :to="{
          path:'/home/message/detail',
          query:{
            id:m.id,
            title:m.title
          }
          // name:'xiJie',
          // params:{
          //   id:m.id,
          //   title: m.title
          // }
        }">
          {{m.title}}
        </router-link>
        <button @click="pushShow(m)">push查看</button>
        <button @click="replaceShow(m)">replace查看</button>
      </li>
      <hr>
      <router-view></router-view>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Message",
  data(){
    return {
      messageList:[
        {id:'001',title:'消息001'},
        {id:'002',title:'消息002'},
        {id:'003',title:'消息003'}
      ]
    }
  },
  methods:{
    pushShow(m){
      // console.log(this.$router
      this.$router.push({
        name:'xiJie',
        query:{
          id:m.id,
          title:m.title
        }
      })
    },
    replaceShow(m){
      this.$router.replace({
        name:'xiJie',
        query:{
          id:m.id,
          title:m.title
        }
      })
    }
  },
  mounted() {
    console.log(this.$router)
  }
}
</script>

